import React, {
    ChangeEvent,
    DetailedHTMLProps,
    FocusEvent,
    InputHTMLAttributes,
    KeyboardEvent,
    useEffect,
    useRef
} from "react";
import s from "./SuperInput.module.css";

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & { //  +       
    value: string
    onChangeText: (value: string) => void
    label: string
    onEnter?: () => void
    error?: string
    spanClassName?: string

};

const SuperInput: React.FC<SuperInputTextPropsType> = (props) => {
    const {
        type, //          
        onChange, onChangeText,
        onKeyPress, onEnter,
        error,
        className, spanClassName,
        value, placeholder,
        label,
        ...restProps//       restProps
    } = props


    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange //    onChange
        && onChange(e); //     ( onChange  )

        onChangeText && onChangeText(e.currentTarget.value);
    }
    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);

        e.key === "Enter" //    Enter
        && onEnter //    onEnter
        && onEnter(); //   
    }

    const finalSpanClassName = `${s.error} ${spanClassName ? spanClassName : ""}`;
    const finalInputClassName = `${s.errorInput} ${s.form__field} ${className}`; // need to fix with (?:) and s.superInput


    return (
        <>
            <div className={s.form__group}>
                <input
                    onKeyPress={onKeyPressCallback}
                    className={finalInputClassName}
                    placeholder={placeholder || 'title'}
                    value={value}
                    onChange={onChangeCallback}/>
                <label className={value === '' ? s.form__label : s.form__label2}>{label || 'title'}</label>
            </div>
            {error && <span className={finalSpanClassName}>{error}</span>}
        </>

    );
}

export default SuperInput;
